import React from "react";
import { Link } from "react-router-dom";
import { dt } from "../../../../i18n";

const CategoryCard = ({ block }) => {
	if (!block) {
		return "";
	}

	return block.dynamicCollectionBlock.map((item, i) => {
		return (
			<Link
				to={`/category/${item.photos[0]?.link}`}
				key={i}
				className="category-card"
			>
				<img
					src={!item.photos ? "" : item.photos[0]?.urls.original}
					alt={item.name}
				/>

				<span>{dt(item.name)}</span>
			</Link>
		);
	});
};

export default CategoryCard;
